//
// Icons
// --------------------------------------------------

// Base styles
// --------------------------------------------------

.icon {
  display: inline-block;
  text-align: center;
  &:before {
    display: inline-block;
    font-weight: 400;
    font-style: normal;
    speak: none;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

[class*='icon-circle'] {
  border-radius: 50%;
  overflow: hidden;
}

[class*='icon-round'] {
  border-radius: 4px;
  overflow: hidden;
}

// Alternate icons
// --------------------------------------------------
.page {
  .icon-default {
    color: $body-color;
  }

  .icon-black {
    color: $black;
  }

  .icon-primary {
    color: $primary;
  }

  .icon-gunsmoke {
    color: $gunsmoke;
  }

  .icon-tundora {
    color: $tundora;
  }

  // Fill
  .icon-gray-dark-filled {
    color: $white;
    background: $gray-dark;
  }

  .icon-silver-chalice-filled {
    color: $white;
    background: $silver-chalice;
  }

  .icon-white {
    color: $white;
  }

  a {

    &.icon-default {
      @include link($body-color, $primary);
    }

    &.icon-primary {
      @include link($primary, $white);
    }

    &.icon-tundora-inverse {
      @include link($tundora, $white);
    }

    &.icon-gray-dark-filled {
      &,
      &:active,
      &:focus {
        color: $white;
        background: $gray-dark;
      }

      &:hover {
        color: $white;
        background: $primary;
      }
    }

    &.icon-silver-chalice-filled {
      &,
      &:active,
      &:focus {
        color: $white;
        background: $silver-chalice;
      }

      &:hover {
        color: $white;
        background: $primary;
      }
    }

    &.icon-pciked-bluewood {
      background: $brand-picked-bluewood;
      color: $white;

      &:hover {
        background: $primary;
      }
    }
  }
}

// Button Sizes
// --------------------------------------------------
.page {
  .icon-xxs {
    @include icon-sizing(18px);

    &-small {
      @include icon-sizing(16px);
    }

    &-smaller {
      @include icon-sizing(14px);
    }

    &-smallest {
      @include icon-sizing(12px);
    }
  }

  .icon-xs {
    @include icon-sizing(22px);

    &-smaller {
      @include icon-sizing(20px);
    }
  }

  .icon-sm {
    @include icon-sizing(24px);

    &-custom {
      @include icon-sizing(24px);
      @include media-breakpoint-up(lg) {
        @include icon-sizing(30px);
      }
    }
  }

  .icon-md {
    @include icon-sizing(36px);

    &-custom {
      @include icon-sizing(26px);
      @include media-breakpoint-up(lg) {
        @include icon-sizing(36px);
      }
    }

    &-smaller {
      @include icon-sizing(30px);
    }
  }

  .icon-lg {
    @include icon-sizing(48px);

    &-variant-1 {
      @include icon-sizing(42px);
    }

    &-variant-2 {
      @include icon-sizing(44px);
    }

    &-bigger {
      @include icon-sizing(55px);
    }
  }

  .icon-xl {
    @include icon-sizing(60px);
  }

  [class*='icon-round'],
  [class*='icon-circle'] {
    &.icon-xxs-smallest {
      width: 26px;
      height: 26px;
      line-height: 26px;
    }
  }
}

// Mods
.icon-shift-1 {
  position: relative;
  top: 2px;
}

.icon-shift-2 {
  position: relative;
  top: 2px;

  @include media-breakpoint-up(lg) {
    top: 4px;
  }
}

